    "code": "import React from 'react';\nimport PropTypes from 'prop-types';\nimport styled from 'styled-components';\nimport * as Styles from './style';\nimport ChevronDown from '../Icon/Icons/ChevronDown';\nimport Select from '../Select/Select';\n\nexport const ButtonStyled = styled.button`\n  ${Styles.ButtonBase};\n  ${props => Styles[props.size]};\n  ${props => Styles[props.disabled ? 'disabled' : props.type]};\n  ${props => Styles[props.fullWidth ? 'fullWidth' : '']}\n`;\n\nconst Loading = styled.img`\n  width: 24px;\n  margin-left: 10px;\n`;\n\nconst VisuallyHiddenLabel = styled.span`\n  position: absolute;\n  overflow: hidden;\n  clip: rect(0 0 0 0);\n  height: 1px;\n  width: 1px;\n  margin: -1px;\n  padding: 0;\n  border: 0;\n`;\n\n/** All buttons, including text, link and split-buttons, follow the same core principles in dimensions, padding, and font sizes.\n * Combined with simple modifiers, they can be changed in size and appearance.  */\nconst Button = ({\n  disabled,\n  onClick,\n  type,\n  size,\n  label,\n  isSplit,\n  loading,\n  icon,\n  iconEnd,\n  hasIconOnly,\n  isSelect,\n  items,\n  selectPosition,\n  onSelectClick,\n  fullWidth,\n  tooltip,\n  ref,\n  hideSearch,\n  className,\n  children,\n}) => (\n  <ButtonStyled\n    onClick={!disabled ? onClick : undefined}\n    disabled={disabled}\n    size={size}\n    type={type}\n    isSplit={isSplit}\n    icon={icon}\n    hasIconOnly={hasIconOnly}\n    fullWidth={fullWidth}\n    data-tip={tooltip}\n    ref={ref}\n    className={className}\n  >\n    {!iconEnd && icon}\n    {hasIconOnly && <VisuallyHiddenLabel>{label}</VisuallyHiddenLabel>}\n    {!hasIconOnly && (\n      <Styles.ButtonLabel hasIcon={!!icon} iconEnd={!!iconEnd}>{label}</Styles.ButtonLabel>\n    )}\n    {iconEnd && icon}\n\n    {isSelect && (type === 'primary' || type === 'secondary') && (\n      <Styles.ButtonArrow>\n        <ChevronDown\n          color={type === 'primary' ? 'white' : 'grayDark'}\n          size={size}\n          isChevron\n        />\n      </Styles.ButtonArrow>\n    )}\n\n    {loading && <Loading src=\"./images/loading-gray.svg\" alt=\"loading\" />}\n\n    {isSplit && (type === 'primary' || type === 'secondary') && (\n      children ?\n        React.Children.map(children, (child) => React.cloneElement(child, { }))\n        : (\n          <Select\n            onSelectClick={onSelectClick}\n            items={items}\n            type={type}\n            isSplit\n            yPosition={selectPosition}\n            xPosition=\"right\"\n            disabled={disabled}\n            hideSearch={hideSearch}\n          />\n        )\n    )}\n  </ButtonStyled>\n);\n\nButton.propTypes = {\n  /** Is the button disabled */\n  disabled: PropTypes.bool,\n\n  /** Size of the Button */\n  size: PropTypes.oneOf(['small', 'large', 'medium']),\n\n  /** OnClick handler */\n  onClick: PropTypes.func.isRequired,\n\n  /** Button label */\n  label: PropTypes.string,\n\n  /** Type of button */\n  type: PropTypes.oneOf(['link', 'primary', 'secondary', 'text', 'error']),\n\n  /** Is the Button Split  */\n  isSplit: PropTypes.bool,\n\n  /** Is this the Select button with chevron */\n  isSelect: PropTypes.bool,\n\n  /** Is the Button Loading  */\n  loading: PropTypes.bool,\n\n  /** Does the button have only an icon and no label */\n  hasIconOnly: PropTypes.bool,\n\n  /** Icon to show with the label */\n  icon: PropTypes.node,\n\n  /** Icon to show with the label */\n  iconEnd: PropTypes.bool,\n\n  /** Items to display in the Split Button popup */\n  items: PropTypes.arrayOf(\n    PropTypes.shape({\n      id: PropTypes.string,\n      title: PropTypes.string,\n    })\n  ),\n\n  /** Child element(s) to use for custom Split Button child content */\n  children: PropTypes.element,\n\n  /** Position of the Select popup */\n  selectPosition: PropTypes.oneOf(['top', 'bottom']),\n\n  /** Function to call on Split Button selected item click */\n  onSelectClick: PropTypes.func,\n\n  /** Is the button the full width of the parent container */\n  fullWidth: PropTypes.bool,\n\n  /** Tooltip to show on the component */\n  tooltip: PropTypes.string,\n\n  /** The prop to get the DOM element of the Button */\n  ref: PropTypes.node,\n\n  /** Is search hidden */\n  hideSearch: PropTypes.bool,\n\n  /** class passed by the dom element */\n  className: PropTypes.string,\n};\n\nButton.defaultProps = {\n  disabled: false,\n  isSplit: undefined,\n  loading: false,\n  size: 'medium',\n  type: 'secondary',\n  label: undefined,\n  hasIconOnly: false,\n  icon: undefined,\n  iconEnd: false,\n  isSelect: undefined,\n  items: undefined,\n  selectPosition: 'bottom',\n  onSelectClick: undefined,\n  fullWidth: false,\n  tooltip: undefined,\n  ref: undefined,\n  hideSearch: true,\n  className: undefined,\n  children: undefined,\n};\n\nexport default Button;\n",
