    "code": "import React from 'react';\nimport PropTypes from 'prop-types';\nimport { SearchInput, SearchWrapper } from './style';\n\n/** Search input that filters the Select items and adds keyboard navigation */\nexport default class Search extends React.Component {\n  state = {\n    search: '',\n  };\n\n\n  componentDidUpdate(prevProps) {\n    if(prevProps.isOpen !== this.props.isOpen){\n      setTimeout(()=> this.inputRef.focus(), 50)\n    }\n  }\n\n\n  onChange = (event) => {\n    const { onChange } = this.props;\n    const search = event.target.value;\n\n    onChange(search);\n    this.setState({\n      search,\n    });\n  }\n\n  onBlur = () => {\n    const { onChange } = this.props;\n\n    onChange('');\n    this.setState({\n      search: '',\n    });\n  }\n\n  render() {\n    const {\n      placeholder,\n      onClick,\n      height,\n      clearSearchOnBlur,\n    } = this.props;\n\n    const { search } = this.state;\n\n    return (\n      <SearchWrapper>\n        <SearchInput\n          placeholder={placeholder}\n          type=\"text\"\n          value={search}\n          ref={inputRef => this.inputRef = inputRef}\n          onChange={event => this.onChange(event)}\n          onClick={onClick}\n          clearSearchOnBlur={clearSearchOnBlur}\n          onBlur={clearSearchOnBlur ? this.onBlur : undefined}\n          height={height}\n        />\n      </SearchWrapper>\n    );\n  }\n}\n\nSearch.propTypes = {\n  /** Search placeholder */\n  placeholder: PropTypes.string,\n\n  /** Function to call on search input change */\n  onChange: PropTypes.func.isRequired,\n\n  /** Is the select menu open */\n  isOpen: PropTypes.bool,\n\n  /** Is onClick event */\n  onClick: PropTypes.func,\n\n  /** Input height */\n  height: PropTypes.string,\n\n  /** Should the search clear on blur */\n  clearSearchOnBlur: PropTypes.bool,\n\n  /** Is onBlur event */\n  onBlur: PropTypes.func\n};\n\nSearch.defaultProps = {\n  placeholder: '',\n  height: 'tall',\n  isOpen: false,\n  clearSearchOnBlur: false,\n  onClick: () => {},\n  onBlur: () => {}\n}\n",
