      "methods": [],
      "code": "import React from 'react';\nimport Select from '@bufferapp/ui/Select';\nimport MoreIcon from '@bufferapp/ui/Icon/Icons/More';\n\n/** With Icon Only */\nexport default function ExampleSelect() {\n  return (\n    <Select\n      onSelectClick={() => true}\n      label=\"Click me\"\n      icon={<MoreIcon />}\n      type=\"primary\"\n      items={[\n        { id: '1', title: 'Open' },\n        { id: '2', title: 'Pending' },\n        { id: '3', title: 'Closed' },\n      ]}\n      hasIconOnly\n      position=\"right\"\n      hideSearch\n    />\n  );\n}\n",
