      "code": "import React from 'react';\nimport Select from '@bufferapp/ui/Select';\nimport { Person } from '@bufferapp/ui/Icon';\n\n\n/** With Icon */\nexport default function ExampleSelect() {\n  return (\n    <Select\n      onSelectClick={() => true}\n      label=\"Click Me\"\n      icon={<Person color=\"white\" />}\n      type=\"primary\"\n      items={[\n        { id: '1', title: 'Open' },\n        { id: '2', title: 'Pending' },\n        { id: '3', title: 'Closed' },\n      ]}\n    />\n  );\n}\n",
